.intro {
    @include fc-center;
    width: 100%;

    &__container {
        @include fc-center;
        width: 100%;
        background-color: $paleWhite;
        background-image: url(../assets/images/intro-clouds.svg);
        background-position: left;
        background-repeat: no-repeat;
        background-size: contain;
        position: relative;
        min-height: 570px;
    }

    &__ellipse {
        width: 100px;
        height: 100px;
        background-image: url(../assets/images/intro-ellipse.svg);
        background-position: left;
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        right: 0;
        bottom: 108px;
    }

    &__chart {
        max-height: 465px;
        height: 100vh;
        width: 100vw;
        max-width: 1440px;
        background-image: url(../assets/images/intro-chart.svg);
        background-repeat: no-repeat;
        background-position: left;
        position: absolute;
        bottom: 0;
    }

    &__textArea {
        @include fc-center;
        margin: 91px 0 0 0;
    }

    &__subtitle {
        @include font-rn500;
        @include mp-0;
        font-size: 10px;
        line-height: 12px;
        text-transform: uppercase;
        color: $darkBlue;
        width: 90px;
        text-align: center;
    }

    &__title {
        @include font-qn300;
        @include mp-0;
        font-size: 48px;
        line-height: 60px;
        color: $darkBlue;
        width: 581px;
        text-align: center;
        margin: 16px 0 0 0;
    }

    &__text {
        @include font-rn400;
        @include mp-0;
        font-size: 15px;
        line-height: 29px;
        color: $paleBlue;
        width: 502px;
        text-align: center;
        margin: 28px 0 0 0;
    }

    &__playBtn-shape {
        @include mp-0;
        width: 52px;
        height: 52px;
        border-radius: 50%;
        background-color: $white;
        margin: 20px 0 0 0;
        position: relative;
        border: none;
        cursor: pointer;
        z-index: 1;

        &:hover {
            opacity: 0.8
        }
    }
    
    &__playBtn-playIcon {
        @include mp-0;
        height: 10px;
        width: 11px;
        background-image: url(../assets/images/play-icon.svg);
        background-repeat: no-repeat;
        background-size: contain;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &__circle-shape {
        @include mp-0;
        box-sizing: border-box;
        width: 15px;
        height: 16px;
        background: $white;
        border: 2px solid $lightBlue;
        box-shadow: 0px 0px 15px rgba(82, 131, 255, 0.25);
        border-radius: 50%;
        position: absolute;
        bottom: 68px;
        left: 508px;
    }

    &__one-of-stats {
        @include mp-0;
        height: 120px;
        width: 140px;
        background-image: url(../assets/images/intro-one-of-stats.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-color: transparent;
        position: absolute;
        bottom: 60px;
        left: 446px;
    }

    &__circleSet {
        @include fr-center;
        @include mp-0;
        width: 48px;
        justify-content: space-between;
        justify-self: flex-end;
        margin: 0 0 28px 0;
        margin-top: auto;
    }

    &__circleSet-item {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: $grey;
    }
}