.getStarted {
  @include fc-center;
  width: 100%;
  background-color: #5e80ff;
  background-image: url(../assets/images/getstarted-clouds.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;

  &__container {
    @include fc-center;
    width: 100%;
    min-height: 447px;
  }

  &__textArea {
    @include fc-center;
    margin: 91px 0 0 0;
  }

  &__subtitle {
    @include font-rn500;
    @include mp-0;
    font-size: 10px;
    line-height: 12px;
    text-transform: uppercase;
    color: $white;
    width: 90px;
    text-align: center;
    letter-spacing: 0.1em;
  }

  &__textAreaWrapper {
    @include fr-center;
    width: 357px;
    justify-content: space-between;
  }

  &__h3Title {
    @include font-qn300;
    @include mp-0;
    font-size: 48px;
    line-height: 60px;
    color: $white;
    text-align: center;
    margin: 16px 0 0 0;

    &_bold {
      @include font-qn700;
    }
  }

  &__text {
    @include font-rn400;
    @include mp-0;
    font-size: 15px;
    line-height: 29px;
    color: $white;
    width: 712px;
    text-align: center;
    letter-spacing: 0.03em;
    margin: 28px 0 0 0;
  }

  &__wrapper {
    @include fr-center;
    margin: 58px 0 0 0;
  }

  &__btn {
    @include mp-0;
    @include font-qn500;
    font-size: 13px;
    line-height: 16px;
    color: #c7d3ff;
    border: 1px solid #a0b4ff;
    background-color: transparent;
    border-radius: 2px;
    width: 155px;
    height: 43px;
    cursor: pointer;

    &_bgWhite {
      @include font-qn700;
      color: #565f82;
      background-color: $white;
      border-style: none;
      box-shadow: 0px 5px 15px #6480e8;
      width: 130px;
    }

    &:hover {
      opacity: 0.8;
    }
  }
}
